<template lang="pug">
transition(name='calendar')    
  .calendar-container
    side-menu
    header-logo
    .calendar.susy-main
      .cal-header
        span.current-cal-header(v-text="calHeader")        
        .cal-header-button-group
          button.prev-month-button(@click="clickPrevCalendar")
            | <
          button.next-month-button(@click="clickNextCalendar")
            | >
      .cal-table
        table
          colgroup
            col(width='100')
            col(width='100')
            col(width='100')
            col(width='100')
            col(width='100')
            col(width='100')
            col(width='100')
          thead
            tr
              td(v-for="day in arrDay") {{ day }}
          tbody(v-for="n in 5")
            tr
              td.cal-date(v-for="m in 7" :class="arrIsThisMonth[ (n-1)*7 + m-1 ]")

                a.date-has-data(v-if="dataSet && dataSet.has(arrTargetDate[(n-1)*7 + m-1].toISOString().split('T')[0])" @click.prevent="clickTargetDate(arrTargetDate[ (n-1)*7 + m-1 ])") {{ arrTargetDate[ (n-1)*7 + m-1 ].getDate() }}
                
                a.date-no-data(href v-else="dataSet && dataSet.has(arrTargetDate[(n-1)*7 + m-1].toISOString().split('T')[0])" @click.prevent="clickTargetDate(arrTargetDate[ (n-1)*7 + m-1 ])") {{ arrTargetDate[ (n-1)*7 + m-1 ].getDate() }}
</template>

<script>
import {mapGetters, mapMutations, mapActions} from 'vuex'

import HeaderLogo from './Header'
import SideMenu from './SideMenu'
import Search from './Search'

export default {
  name: 'Calendar',
  components: {
    HeaderLogo, SideMenu, Search
  },
  created() {
    this.showSearchBtn();
    this.getAllData();
    this.makeCalendar();
  },
  data() {
    return {
      currentMonth: new Date(),
      calHeader: '',
      arrDay: ["SUN", "MON", "TUE", "WED", "THU", "FRI", "SAT"],
      arrIsThisMonth : [],
      arrTargetDate: [],
      targetFullDate: '',
      dayListUrl: '',
      dataSet: null,
      datedatedate: [],
      hasDate: []
    }
  },
  methods: {
    ...mapMutations([
      'showSearchBtn'
      // 'showAllDayData',
      // 'getCalPhoto'
    ]),
    //    
    makeCalendar() {
      //  
      let date = this.currentMonth;
      let arrMonth = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];

      //   ( , )
      this.calHeader = arrMonth[date.getMonth()] + ' ' + date.getFullYear();

      //   1 
      date.setDate(1);

      //     
      // cf. 2017 8  , 7 30
      //   1  1  index 
      let targetDate = new Date(date.getTime());
      targetDate.setDate(targetDate.getDate() - targetDate.getDay());

      //    arrIsThisMonth, arrTargetDate 
      this.arrIsThisMonth = [];
      // this.arrTargetDate = [];

      this.arrTargetDate = [];

      //    targetDate   
      //   max 42(6*7)
      for(let i=0; i<42; i++) {
        //       
        // ,  : not-this-month / : this-month
        let isThisMonth = "";
        if (date.getMonth() !== targetDate.getMonth()) {
          isThisMonth = 'not-this-month';
        } else {
          isThisMonth = 'this-month';
        };

        //      arrIsThisMonth ()
        this.arrIsThisMonth.push(isThisMonth);

        //    target date  arrTargetDate 
        this.arrTargetDate.push(new Date(targetDate));

        //    (+1)
        targetDate.setDate(targetDate.getDate() + 1);

        //  
        //      
        if(i % 7 ==6) {
          if(date.getMonth() != targetDate.getMonth()) {
            break;
          }
        }
      }
    },
    //    
    clickPrevCalendar() {
      let date = this.currentMonth;
      date.setMonth(date.getMonth() - 1);
      this.makeCalendar();
    },
    //    
    clickNextCalendar() {
      let date = this.currentMonth;
      date.setMonth(date.getMonth() + 1);
      this.makeCalendar();
    },
    //    YYYYMMDD     URL   
    clickTargetDate(target_date) {
      // 1.    YYYYMMDD  
      //   , ,    
      let object_year = target_date.getFullYear();
      let object_month = target_date.getMonth() +1;
      let object_date = target_date.getDate();
      // Year (  )
      let string_year = String(object_year);
      // Month (  0    )
      if ( object_month < 10 ) {
        object_month = '0' + object_month;
      }
      let string_month = String(object_month);
      // Date (  0    )
      if ( object_date < 10) {
        object_date = '0' + object_date;
      }
      let string_date = String(object_date);
      // YYYYMMDD   fullDate
      let fullDate = string_year + string_month + string_date;
      this.targetFullDate = fullDate;

      // 2.    daylist url  
      let target_url_daylist = this.$store.state.url_daylist + fullDate + '/';
      // data  daylist url 
      this.dayListUrl = target_url_daylist;

      this.getDayList();
    },
    // Post  (   )
    //   params targetFullDate  (  YYYYMMDD  )
    getDayList() {
      let user_token = window.localStorage.getItem('token');

      this.$http.get(this.dayListUrl, {
        headers: { 'Authorization' : `Token ${user_token}` }  
      })
      .then(response => {
        this.$router.push({
          name: 'Post',
          params: {
            date: this.targetFullDate
          }
        })
      })
      .catch(error => {
        console.log(error);
      })
    },
    getAllData() {
      let list_url = this.$store.state.url_post;
      let user_token = window.localStorage.getItem('token');
      this.$http.get(list_url, {
        headers: { 'Authorization' : `Token ${user_token}` }  
      })
      .then(response => {
        let all_data = response.data;
        this.dataSet = new Set();
        all_data.forEach((item) => {
          this.dataSet.add(item.date.split(' ')[0]);
        });
      })
      .catch(error => {
        console.log(error);
      })
    }
  }
}
</script>





