<template lang="pug">
transition(name='signin' mode="out-in")  
  .signin-container.susy-user
    main.signin-content-wrapper
      .signin-content-wrapper-inside
        button.signin-content-wrapper-inside-delete(
          type='button'
          @click='closeSignIn') X
        .signin-content-buttons
          button.signin-content-buttons-signin(type='button') 
          button.signin-content-buttons-signup(
            type='button'
            @click='openSignUp') 
        .signin-input
          form
            legend    
            fieldset
              label(for='signin-email')
                input(
                  v-model.trim='email'
                  id='signin-email'
                  type='email'
                  ref='email'
                  @input.lazy='emailValidation'
                  @keydown.enter='loginValid'
                  placeholder='  .'
                  aria-label=' '
                  required
                  autofocus)
              div.email-valid-false(v-show='email_valid_false')
                span.fa.fa-exclamation-circle(aria-hidden="true")  {{ email_valid_warning }}
              label(for='signin-password')
                input(
                  v-model.trim='password'
                  id='signin-password' 
                  type='password' 
                  ref='password' 
                  @input.lazy='passwordValidation'
                  @keydown.enter='loginValid'                  
                  placeholder='  .'
                  aria-label=' '                
                  required)
              div.password-valid-false(v-show='password_valid_false')
                span.fa.fa-exclamation-circle(aria-hidden="true")  {{ password_valid_warning }}                  
          .signin-buttons-signin
            button.signin-login(
              type='button'
              @click='loginValid')
              span 
            //- .fb-login-button(
            //-   data-max-rows='1', 
            //-   data-size='large', 
            //-   data-button-type='login_with', 
            //-   data-show-faces='false', 
            //-   data-auto-logout-link='false', 
            //-   data-use-continue-as='false')
            button.signin-facebook(
              @click='facebookToken')
              span.fa.fa-facebook-official  
              span.signin-facebook-text  
          hr
          p.signin-notice-facebook Facebook     .
</template>

<script>
import {mapGetters, mapMutations} from 'vuex'

let emailRegex = /^[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_\.]?[0-9a-zA-Z])*\.[a-zA-Z]{2,3}$/i;
let passwordRegex = /^.*(?=.{8,20})(?=.*[0-9])(?=.*[a-zA-Z]).*$/;

export default {
  name: 'SignIn',
  data() {
    return{
      email: '',
      email_valid_warning: '',
      email_valid_false: false,
      password: '',
      password_valid_warning: '',
      password_valid_false: false,
    }
  },
  computed: {
    ...mapGetters([
      'signUp',
      'signIn',
      'mainView',
      'getUrlLogin',
      'getUrlValid',
    ]),
  },
  methods: {
    ...mapMutations([
      'openSignUp',
      'closeSignIn',
    ]),
    emailValidation() {
      if( !emailRegex.test(this.email) ) {
        this.email_valid_false = true
        this.email_valid_warning = '  .'
        this.$refs.email.focus()
        return false
      } else if ( emailRegex.test(this.email) ) {
        this.email_valid_false = false  
        return true
      }
    },
    passwordValidation() {
      if ( !passwordRegex.test(this.password) ) {
        this.password_valid_false = true
        this.password_valid_warning = ' 8 ,     .'
        this.$refs.password.focus();     
        return false
      } else if ( passwordRegex.test(this.password) ) {
        this.password_valid_false = false
        return true
      }
    },
    submitSignIn() {
      if(!this.emailValidation()) return
      if(!this.passwordValidation()) return
      this.$http.post(this.getUrlLogin, {
        email:    this.email,
        password: this.password
      })
      .then(response => {
        let token = response.data.key;
        window.localStorage.setItem('token', token);
        this.$router.push({
          path: '/calendar',
          // name: 'Calendar',
          //   params: {
          //     email: this.email
          //   }
        });
        let user_pk = response.data.user.pk
        window.localStorage.setItem('user_pk', user_pk);
      })
      .catch(error => {
        console.log(error.response)
        window.alert(' &  ')
      })
    },
    loginValid() {
      this.$http.get(this.getUrlValid, {
        params: { 
          email: this.email
        }
      })
      .then(response => {
        this.submitSignIn()
        let error_valid = response.data;
        let props = Object.values(error_valid);
        console.log(props);
        if ( props[0] ) {
          window.alert('  .')  
        }
      })
      .catch(error => {
        console.log(error.response)
      })
    },
    facebookToken() {
      var FB = window.FB;
      var scopes = 'email,public_profile';
      FB.login((response) => {
        if (response.status === 'connected') {
          console.log('The user has logged in!');
          FB.api('/me', (response) => {
            console.log('  ', response);
          });
        }
      }, { scope: scopes });
      FB.getLoginStatus( (response) => {
        
        if (response.status === 'connected') {
          console.log('Logged in.');
          console.log('response.authResponse.accessToken:', response.authResponse.accessToken);
          let access_token = response.authResponse.accessToken;
          this.$http.post(this.getUrlLogin + 'facebook/', {
            'token': access_token
          }).then((response) => {
            console.log('response.data.key:', response.data.key);
            console.log('response.data.user:', response.data.user);
            window.localStorage.setItem('token', response.data.key);
            window.localStorage.setItem('facebook', access_token);
            window.localStorage.setItem('user_pk', response.data.user.pk);
            console.log('access_token', access_token);
            
            console.log('  ', response);
            this.$router.push({
              path: '/calendar'
              });
          }).catch((error) => {
            console.log('error:', error)
         });
        } else if (response.status === 'not_authorized') {
          //        
          window.location.replace('/');
          console.log('  ');
        } else {
          console.log('unknown');
        }
      });
    },
  }
}
</script>

<style lang="sass" scoped>
  
  body
    overlfow: hidden

</style>


